.container{
    text-align: center;
    height: 100%;
}

.container p{
    color: #957DAD;
    font-size: 40px;
    font-weight: bold;
    text-align: center;
    padding-top: 150px;
}


.container button{
    color: #E9E2D7;
    background: #704523;
    font-size: 18px;
    font-weight: bold;
    border: 0;
    border-radius: 12px;
    padding: 10px 60px;
    position: fixed;  
    margin-left: -90px;
    bottom: 80px;
}

@media only screen and (max-width: 360px){
    .container p{
        font-size: 24px;
        padding-top: 50px;
    }

    .container button{
        font-size: 14px;
        bottom: 60px;
    }
}
